export const TINT_COLOR = "#ed674f"
// export const TINT_COLOR = "#5696E7" // Blue

export const EYRIE_URL = process.env.NODE_ENV === "production"
  ? "https://eyrie.coden.hk"
  : "http://localhost:3001"

export function clsConcat(...args: string[]) {
  return args.join(" ")
}

export function capitalizeFirstLetter(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}
